body {
	padding-top: 75px;
}

.search-container {
	width: 490px;
	margin: 0 auto;
}

input#search-bar {
	margin: 0 auto;
	width: 100%;
	height: 45px;
	padding: 0 20px;
	font-size: 1rem;
	border: 1px solid #d0cfce;
	outline: none;
	&:focus {
		border: 1px solid #008abf;
		transition: 0.35s ease;
		color: #008abf;
		&::-webkit-input-placeholder {
			transition: opacity 0.45s ease;
			opacity: 0;
		}
		&::-moz-placeholder {
			transition: opacity 0.45s ease;
			opacity: 0;
		}
		&:-ms-placeholder {
			transition: opacity 0.45s ease;
			opacity: 0;
		}
	}
}

.search-icon {
	position: relative;
	float: right;
	width: 75px;
	height: 75px;
	top: -62px;
	right: -45px;
}
